






import { Vue, Component } from 'vue-property-decorator';

@Component
export default class Error404 extends Vue {
  timeCountDown: number = 7;
  timer: number = 0;
  beforeMount () {
    this.handleTimeDown();
  }
  backHome () {
    this.$router.push('/');
  }
  handleTimeDown () {
    if (this.timeCountDown < 1) {
      this.backHome();
      return;
    }
    this.timeCountDown--;
    clearTimeout(this.timer);
    this.timer = setTimeout(() => {
      this.handleTimeDown();
    }, 1000);
  }
}
